:root{
  --backgound: #1A1A1A;
  --white: #fafafa;
  --font: 'Montserrat';
  --green: #17b802;
}
body{
  margin: 0;
  font-family: var(--font);
  background-color: #fafafa;
}
.nav-buttons{
  list-style: none;
  display: flex;
  margin: 1em 3em;
  padding: 0;
}

header{
  background-color: var(--backgound);
  margin: 0;
  color: #fafafa;
}

header nav{
  display: flex;
  justify-content: space-between;
}
.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1em 3em 1em 1em;
  cursor: pointer;
  font-size: 1.4rem;
  font-weight: 900;
}

/* .active-page{
  border-bottom: 2px solid ;
  color:YOUR-LINK-COLOR;
  padding-bottom:1px;
} */
.nav-buttons li {
  transition: color 0.1s, background-color 0.1s;
  position: relative;
  display: block;
  padding: 1rem 1rem;
  cursor: pointer;
  margin: 0 12px;
  font-size: 1rem;
  line-height: 16px;
  white-space: nowrap;
  font-weight: 500;
  transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
}
.nav-buttons li::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 3px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--green);
  transform-origin: right top;
  transform: scale(0, 1);
  transition: color 0.1s,transform 0.2s ease-out;
}
.nav-buttons li:active::before {
  background-color: var(--green);
}
.nav-buttons li:hover::before, a:focus::before {
  transform-origin: left top;
  transform: scale(1, 1);
}
.intro-section{
  height: 95vh;
  width: 100%;
  background-color: var(--backgound);
  padding: 0;
  display: flex;
  justify-content: center;
  margin: 0;
  background-image: url(./iStock-1390616703.png);
  background-repeat: no-repeat;
  background-position: 50% 200%;
  background-size: 90vh;
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
  background-attachment: fixed;
}
.box {
  animation-duration: 2s;
  animation-iteration-count: infinite;
  transform-origin: bottom;
}
.bounce-2 {
  animation-name: bounce-2;
  animation-timing-function: ease;
}
.arrow{
  position: absolute;
  bottom: 60px;
  right: 50%;
  scale: .3;
  /* transform: rotate(90deg); */
  translate: 50%;
  filter: invert(1);
}
@keyframes bounce-2 {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-100px); }
  100% { transform: translateY(0); }
}
.about-me{
  width: 80%;
  margin: 3em 10%;
  height: 100%;
  background-color: var(--white);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.developer{
  color: var(--green);
  margin: auto 1rem;
  max-width: 50%;
  /* min-width: 20em; */
}
.about{
  min-height: 10em;
}
.about h2{
  display: inline-block;
}
h2 span{
  white-space: nowrap;
}
.quote{
  background-color: var(--backgound);
  color: var(--white);
  padding: 2rem;
  max-width: 50%;
  display: flex;
  flex-wrap: wrap;
  min-width: 20em;
  align-items: center;
  font-size: 1.1em;
  min-height: 12rem;
  margin: 0 2rem;
  
}
.quote blockquote{
  font-weight: 400;
  font-style: italic;
  margin: 25px 40px;
  text-align: center;
}
blockquote span{
  color: var(--green);
  font-weight: 500;
  float: right;
  margin: 10px 10px 0;
}
@media (max-width:850px){
  .nav-buttons{
    display: none;
  }
}
.timline-section{
  margin: 5em 20%;
}
.timline-section h2, .projects h2, footer h2{
  color: var(--green);
  font-weight: 700;
  font-size: 2.2em;
}
.timeline{
  display: flex;
  /* flex-wrap: wrap; */
  width: 100%;
  height: 100%;
  margin: 0;
  justify-content: center;
}
.line{
  min-height: 100%;
  /* min-height: 28em; */
  width: 5px;
  border-radius: 5px;
  background-color: var(--backgound);
  margin: 30px 0 0 0;
}
.timeline-ul{
  padding: 0;
}
.pika{
  height: 15px;
  width: 15px;
  background-color: var(--green);
  border-radius: 100%;
  margin: 0 0 0 -10px;
}
.timeline-ul li{
  margin-bottom: 2.5em;
  list-style: none;
  display: flex;
  font-size: 1.1em;
  align-items: center;
  padding: 10px 0;
  min-width: 10em;
}
.date{
  margin: 0 20px;
  width: 5em;
  text-align: center;
  font-weight: 700;
}
a{
  text-decoration: none;
  color: var(--green);
  font-weight: 700;
  font-style: italic;
}
html{
  height: 400vh;
}
.projects{
  padding: 8em 20%;
  width: 60%;
  height: 100%;
  background-color: var(--backgound);
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);

}
.gallery{
  margin-top: 4rem;
  display: flex;
  flex-wrap: wrap;
  filter: grayscale(1);
  justify-content: center;
  align-items: center;
}
.gallery img{
  max-height: 8em;
  max-width: 8em;
  margin: 20px;
}
footer{
  background-color: var(--white);
  width: 60%;
  margin: 5em 20%;
}